import Vue from 'vue'
import App from './App.vue'
import router from './router'

import './js/tableau.extensions.1.latest.min';
import vuetify from './plugins/vuetify'

import AxiosPlugin from 'vue-axios-cors';
 
Vue.use(AxiosPlugin)

Vue.config.productionTip = false


new Vue({
  router,
  vuetify,
  render: h => h(App)
}).$mount('#app')
