<template>
  <v-container class="con">
    <v-row>
      <v-col cols="6">
        <v-text-field
          v-model="search"
          label="Pesquisar Filtros"
          flat
          hide-details
          clearable
          clear-icon="mdi-close-circle-outline"
        ></v-text-field>
      </v-col>
      <v-col cols="6">
        <v-btn class="mr-1 mt-2" elevation="8" @click="reset()"> Reset</v-btn>
        <v-btn @click="toggleTreeview()" elevation="8" class="mr-1 mt-2">
          <v-icon>{{ expanded ? "mdi-arrow-collapse" : "mdi-arrow-expand" }}</v-icon>
        </v-btn>
        <v-btn class="mt-2" elevation="8" @click="showFiltersHistory()">
          <v-icon>{{ "mdi-information-outline" }}</v-icon>
        </v-btn>
        <v-btn class="mr-1 mt-2" elevation="8" @click="logQuery()"> query</v-btn>
      </v-col>
    </v-row>

    <v-treeview
      v-model="selection"
      :items="items2"
      :search="search"
      selectable
      dense
      return-object
      :open-all="expanded"
      selection-type="independent"
      selected-color="red"
      ref="myTreeview"
    >
      <template v-slot:prepend="{ item }">
        <template v-if="item.type == 'Field_Name'">
          <div class="row" style="width: 95px; margin-left: 10px">
            <v-checkbox
              id="checkDad"
              color="red"
              @change="selectByDad($event, item)"
              v-model="checkbosDad[item.id]"
            />
            <v-btn @click="filt(item)" small class="mt-4">
              <v-icon>mdi-table-eye</v-icon>
            </v-btn>
          </div>
        </template>
      </template>

      <!--       <template v-slot:label="{ item }">
        <div style="margin-left: -5px">
          {{ item.name }}
        </div>
      </template> -->

      <template v-slot:append="{ item }" style="width: 3px">
        <template v-if="item.type == 'Field_Name'" style="width: 3px">
          <div class="row" style="width: 95px">
            <v-tooltip top>
              <template v-slot:activator="{ on, attrs }">
                <v-icon class="ml-3" color="primary" dark v-bind="attrs" v-on="on"
                  >mdi-information</v-icon
                >
              </template>
              <span>{{ item.description }}</span>
            </v-tooltip>
          </div>
        </template>
      </template>
    </v-treeview>
  </v-container>
</template>

<script>
import items from "../../public/items.js";
import axios from "axios";

export default {
  data: () => ({
    worksheets: [],
    checkDad: [],
    dashboard: null,
    selection: [],
    array2: [],
    search: null,
    expanded: false,
    simulations: [],
    totalInit: null,
    isReset: true,
    checkbosDad: [],
    selects: [],
    items2: items,
  }),
  methods: {
    logQuery() {
      let query = 'select * from "Simulações" where';
      this.selection.forEach((el) => {
        query += ` ${el.value.replace(/\s/g, "_").toLowerCase() + "__c"} = '${
          el.name
        }' and`;
      });
      console.log(this.selection);
      console.log(query);
    },
    updateParameSelect(fieldName) {
      if (this.selects[1] != fieldName) {
        if (this.selects.length > 1) {
          this.selects.shift();
        }
        this.selects.push(fieldName);
        this.worksheets[0].findParameterAsync("selecionados").then((param) => {
          let str = "";
          this.selects.forEach((el) => {
            str += el + "|";
          });
          param.changeValueAsync(str);
        });
      }
    },
    selectByDad(event, item) {
      event ? (this.checkbosDad[item.id] = true) : (this.checkbosDad[item.id] = false);
      this.countJump_update = 2;
      if (event) {
        this.selection.push(...item.children);
        this.worksheets[1].findParameterAsync("Parâmetro 1").then((param) => {
          param.changeValueAsync(item.name);
        });
      } else {
        // remove the match elements beetwen the array and the selection
        this.selection = this.selection.filter((el) => {
          return !item.children.includes(el);
        });
      }
      this.worksheets.forEach((worksheet) => {
        worksheet.applyFilterAsync(item.name, [], "all");
      });
      this.array2 = this.selection;

      this.updateParameSelect(item.name);
    },
    async filt(item) {
      this.worksheets[1].findParameterAsync("Parâmetro 1").then((param) => {
        param.changeValueAsync(item.name);
      });

      this.updateParameSelect(item.name);
    },
    async showFiltersHistory() {
      let ref = this;
      const popupUrl = `${window.location.origin}/build-claro-extension/#/filtersHistory`;
      //const popupUrl = `${window.location.origin}/#/filtersHistory`;
      ref.worksheets[0].getSummaryDataAsync().then(function (dataTable) {
        dataTable["simulations"] = ref.simulations;
        dataTable["selection"] = ref.selection;
        dataTable["totalInit"] = ref.totalInit;
        tableau.extensions.ui
          .displayDialogAsync(popupUrl, JSON.stringify(dataTable), {
            height: 350,
            width: 500,
          })
          .then((closePayload) => {
            console.log(closePayload);
          })
          .catch((error) => {
            switch (error.errorCode) {
              case tableau.ErrorCodes.DialogClosedByUser:
                console.log("Dialog was closed by user");
                break;
              default:
                console.error(error.message);
            }
          });
      });
    },
    toggleTreeview() {
      this.expanded = !this.expanded;
      this.$refs.myTreeview.updateAll(this.expanded);
    },
    reset() {
      // set false in all elements of array checkboxDad
      this.checkbosDad = this.checkbosDad.map((el) => {
        return false;
      });

      this.worksheets[0].findParameterAsync("selecionados").then((param) => {
        param.changeValueAsync("");
      });

      this.selection = [];
      this.simulations = [];
      this.countJump_update = 1;
      this.isReset = true;
      this.array2 = [];
      this.worksheets[1].findParameterAsync("Parâmetro 1").then((param) => {
        param.changeValueAsync("Selecione a dimensão");
      });
      this.worksheets.forEach((worksheet) => {
        worksheet.getFiltersAsync().then((filters) => {
          filters = filters.filter((el) => {
            return (
              el.fieldName != "Filtro Dimensão 1" && el.fieldName != "Filtro Dimensão 2"
            );
          });
          filters.forEach((filter) => {
            //worksheet.clearFilterAsync(filter._fieldName);
            worksheet.applyFilterAsync(filter._fieldName, [], "all");
          });
        });
      });
      this.worksheets[0].getSummaryDataAsync().then((data) => {
        this.totalInit = data.data[0][0]._formattedValue;
      });
    },
    log() {
      this.worksheets[0].applyFilterAsync(
        "Fx Consumo Globoplay Mb",
        ["0 - SEM INFO"],
        "add"
      );
    },
  },
  watch: {
    selection(val) {
      if (this.countJump_update > 0) {
        this.countJump_update--;
      } else {
        if (val.length > this.array2.length) {
          // Verificar filtros existentes
          this.worksheets[0].getFiltersAsync().then((filter) => {
            // Valor a ser adicionado
            let lastModify = val.filter((a) => !this.array2.includes(a));

            this.updateParameSelect(lastModify[0].value);

            // Verificar se o filtro já existe
            let sameValue_lastModify = filter.filter(
              (a) => a.fieldName === lastModify[0].value
            );

            this.worksheets[1].findParameterAsync("Parâmetro 1").then((param) => {
              param.changeValueAsync(lastModify[0].value);
            });

            // Se existir, adicionar o valor
            let sameValue_lastModify_name = [];
            if (sameValue_lastModify.length > 0 && !sameValue_lastModify.isAllSelected) {
              sameValue_lastModify_name = sameValue_lastModify[0].appliedValues.map(
                (a) => a._formattedValue
              );
            }
            sameValue_lastModify_name.push(lastModify[0].name);

            this.worksheets.forEach((worksheet) => {
              worksheet.applyFilterAsync(
                lastModify[0].value,
                sameValue_lastModify_name,
                "replace"
              );
            });

            this.worksheets[0].getSummaryDataAsync().then((data) => {
              this.simulations.push({
                id: lastModify[0].id,
                name: lastModify[0].value,
                value: lastModify[0].name,
                isAdd: true,
                total: data.data[0][0]._formattedValue,
              });
            });

            this.array2 = val;
          });
        } else {
          this.worksheets[0].getFiltersAsync().then((filter) => {
            let lastModify = this.array2.filter((a) => !val.includes(a));

            let sameValue_lastModify = filter.filter(
              (a) => a.fieldName === lastModify[0].value
            );

            if (sameValue_lastModify[0].appliedValues.length == 1) {
              this.worksheets.forEach((worksheet) => {
                worksheet.applyFilterAsync(lastModify[0].value, [], "all");
              });
            } else {
              this.worksheets.forEach((worksheet) => {
                worksheet.applyFilterAsync(
                  lastModify[0].value,
                  [lastModify[0].name],
                  "remove"
                );
              });
            }

            this.worksheets[0].getSummaryDataAsync().then((data) => {
              this.simulations.push({
                name: lastModify[0].value,
                value: lastModify[0].name,
                isAdd: false,
                total: data.data[0][0]._formattedValue,
              });
            });

            this.array2 = val;
          });
        }
        //this.array2 = val;
      }
    },
  },
  async mounted() {
    let ref = this;
    tableau.extensions.initializeDialogAsync().then(() => {
      ref.worksheets[0] = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "#pessoas"
      );
      ref.worksheets[1] = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "#grafico1.1"
      );
      ref.worksheets[2] = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "#grafico2.1"
      );
      ref.worksheets[0].getSummaryDataAsync().then((data) => {
        ref.totalInit = data.data[0][0]._formattedValue;
      });

      ref.dashboard = tableau.extensions.dashboardContent.dashboard;
    });

    axios({
      method: "post",
      url: "https://login.salesforce.com/services/oauth2/token",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
        "Access-Control-Allow-Origin": window.location.origin,
      },
      data:
        "grant_type=password&username=fiveacts@fiveacts.com.br.1&password=F1v3@123&client_id=3MVG9KsVczVNcM8yl6B0YdulqYRhgVQhG5xbViipV93ulHavQRSoYE0SqtEprkjTPZLrO9V0EjH.L.2HBSPbj&client_secret=610AA2DB0F8B4CBDAFDFE6C4542A674A619502DF6C9CDBCEC21BA766C90D92A0",
    })
      .then(async function (token) {})
      .catch(function (error) {
        console.log("err2:", error);
      });
  },
};
</script>

<style>
button + button.v-treeview-node__checkbox {
  display: none !important;
}

/* .v-treeview-node__root {
  min-height: 0px;
  max-height: 0px;
  padding: 0px;
  margin: 0px;
} */

.v-treeview-node__toggle-mine {
  opacity: 1;
  margin-left: 100px;
  /* let the click events go through to the actual toggle underneath */
  pointer-events: none;
}
</style>
