<template>
  <v-container>
    <h3>Selecione o nome do Parametro.</h3>
    <v-select
      v-model="parameter"
      :items="parameters"
      :rules="[(v) => !!v || 'Selecione uma opção.']"
    ></v-select>
    <div class="row">
      <dic class="col-10"></dic>
      <v-btn class="white--text col-2" color="primary" @click="save()"> Salvar </v-btn>
    </div>
  </v-container>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      parameters: [],
      parameter: null,
    };
  },
  mounted() {
    let ref = this;
    tableau.extensions.initializeDialogAsync().then(() => {
      ref.getParameters();
      //carregar planilhas do dashboard
      let dashboard = tableau.extensions.dashboardContent.dashboard;
      ref.worksheets = dashboard.worksheets.map((worksheet) => worksheet.name);
    });
  },
  /*   async created() {
    await tableau.extensions.initializeDialogAsync();
    
  }, */
  methods: {
    getParameters: async function () {
      const dashboard = tableau.extensions.dashboardContent.dashboard;
      let parameters = await dashboard.getParametersAsync();
      console.log(parameters);
      parameters = parameters.filter(
        (p) => p.dataType === "string" && p.allowableValues.type === "all"
      );
      this.parameters = parameters.map((p) => p.name);

      const settings = tableau.extensions.settings.getAll();
      this.parameter =
        settings.parameter && parameters.find((p) => p.name === settings.parameter)
          ? settings.parameter
          : "";
    },
    save: async function () {
      tableau.extensions.settings.set("parameter", this.parameter);
      tableau.extensions.settings.set("first_setup", "no");
      await tableau.extensions.settings.saveAsync();
      tableau.extensions.ui.closeDialog("");
    },
  },
};
</script>
